.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #2c3e50;
  color: white;
  padding: 1rem;
}

.auth__title {
  margin-bottom: 2rem;
  font-size: 2rem;
  text-align: center;
}

.auth__form {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  color: #333;
  box-sizing: border-box;
}

.auth__form-group {
  margin-bottom: 1.5rem;
}

.auth__form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.auth__form-input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  box-sizing: border-box;
}

.auth__button {
  width: 100%;
  padding: 0.75rem;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.auth__button:hover {
  background-color: #34495e;
}

.auth__error {
  color: #e74c3c;
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 600px) {
  .auth__form {
    padding: 1rem;
    max-width: 100%;
  }

  .auth__title {
    font-size: 1.5rem;
  }
}
