/* Base */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
}

/* Layout */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header */
.app__header {
  background-color: #2c3e50;
  color: white;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  gap: 0.5rem;
}

.app__title {
  margin: 0;
  font-size: 1.5rem;
}

/* Button */
.app__logout-button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.app__logout-button:hover {
  background-color: #c0392b;
}

/* Navigation */
.app__nav {
  background-color: #34495e;
  padding: 0.5rem 1rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.app__nav-link {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.app__nav-link:hover {
  background-color: #2c3e50;
}

/* Main content */
.app__main {
  flex: 1;
  padding: 1rem;
}

/* Responsive */
@media (max-width: 740px) {
  .app__nav {
    flex-direction: column;
    gap: 0.5rem;
  }

  .app__nav-link {
    padding: 0.75rem 1rem;
    text-align: center;
  }

  .app__header {
    flex-direction: column;
  }
}
