.student-list {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.student-list__form-section {
  flex: 1;
  padding-right: 40px;
}

.student-list__form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.student-list__input,
.student-list__search {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  width: 100%;
  max-width: 400px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

.student-list__submit {
  padding: 10px 15px;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.student-list__submit:hover {
  background-color: #34495e;
}

.student-list__group {
  margin-bottom: 25px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 15px;
  background-color: #f9f9f9;
}

.student-list__group-header {
  margin-top: 0;
  padding-bottom: 8px;
  border-bottom: 1px solid #ddd;
  color: #2c3e50;
}

.student-list__items {
  list-style: none;
  padding: 0;
}

.student-list__item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.student-list__item:hover {
  background-color: #f9f9f9;
}

.student-list__info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.student-list__name {
  font-weight: 500;
}

.student-list__delete {
  background: #e74c3c;
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s, transform 0.2s;
}

.student-list__delete:hover {
  background: #c0392b;
  transform: scale(1.1);
}

.student-list__logo {
  width: 300px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 20px;
}

.student-list__logo-img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

/* адаптив */
@media (max-width: 768px) {
  .student-list {
    flex-direction: column;
  }

  .student-list__form-section {
    padding-right: 0;
  }

  .student-list__logo {
    width: 100%;
    justify-content: center;
    margin-top: 20px;
  }

  .student-list__group {
    padding: 10px;
  }
}
