.schedule-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.schedule-page__nav-link {
  display: inline-block;
  margin-bottom: 20px;
  color: #333;
  text-decoration: none;
}

.schedule-page__input-wrapper {
  margin: 20px 0;
  width: 100%;
}

.schedule-page__input {
  padding: 8px;
  width: 100%;
  max-width: 300px;
  box-sizing: border-box;
}

.schedule-page__week {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.schedule-page__day {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  background: #f9f9f9;
}

.schedule-page__subject-list {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

.schedule-page__subject-item {
  display: flex;
  justify-content: space-between;
  padding: 8px;
  background: white;
  margin: 5px 0;
  border-radius: 4px;
}

.schedule-page__button {
  cursor: pointer;
  border: none;
  background: #2c3e50;
  color: white;
  border-radius: 4px;
  padding: 5px 10px;
}

.schedule-page__button--delete {
  background: #f44336;
}

/* Адаптив */
@media (max-width: 768px) {
  .schedule-page {
    padding: 10px;
  }

  .schedule-page__week {
    grid-template-columns: 1fr;
  }

  .schedule-page__input {
    width: 100%;
  }
}
